$particleSize: 35vmin;
$animationDuration: 12s;
$amount: 100;
 .parti {
  width: $particleSize;
  height: $particleSize;
  border-radius: $particleSize;
  backface-visibility: hidden;
  position: absolute;
  animation-name: move;
  animation-duration: $animationDuration;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  $colors: (
    rgba($color: #9A67A5, $alpha: 0.4),
    rgba($color: #FF6F91, $alpha: 0.4),
    rgba($color: #257A3E, $alpha: 0.4),
    rgba($color: #9BDEAC, $alpha: 0.4),
    rgba($color: #309CFF, $alpha: 0.4),
    rgba($color: #F7F5DD, $alpha: 0.4),
    rgba($color: #FFCFFF, $alpha: 0.4),
  );
  @for $i from 1 through $amount {
    &:nth-child(#{$i}) {
      color: nth($colors, random(length($colors)));
      top: random(100) * 1%;
      left: random(100) * 1%;
      animation-duration: (random($animationDuration * 10) / 10) * 1s + 10s;
      animation-delay: random(($animationDuration + 10s) * 10) / 10 * -1s;
      transform-origin: (random(50) - 25) * 1vw (random(50) - 25) * 1vh;
      $blurRadius: (random() + 0.5) * $particleSize * 0.5;
      $x: if(random() > 0.5, -1, 1);
      box-shadow: ($particleSize * 2 * $x) 0 $blurRadius currentColor;
    }
  }
}

@keyframes move {
  100% {
    transform: translate3d(0, 0, 1px) rotate(360deg);
  }
}
.Liana, .Software, .about, .connect, .work{
  animation-name: slide;
  opacity: 0;
  animation-timing-function: ease-out;
  animation-iteration-count: 1;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  font-family: 'Questrial', sans-serif;
  font-size: 6em;
  margin-left: 2%;
}
.anim{
  animation-name: slideanim;
  opacity: 0;
  animation-timing-function: ease-out;
  animation-iteration-count: 1;
  animation-duration: 0.7s;
  animation-fill-mode: forwards;
}
.Software{
  animation-delay: 0.16s;
}
.about{
  animation-delay: 0.3s;
}
.connect{
  animation-delay: 0.51s;
}
.work{
  animation-delay: 0.4s;
}
.Liana{
  margin-top: 1.2em;
}
@keyframes slide{
  0% {
    opacity: 0;
    filter: blur(1.5rem);
    font-size: 6.5em;
  }
  20%{
    filter: blur(1.5rem);
    font-size: 6.5em;
    opacity: 1;
  }
  50%{
    filter: blur(0.8rem);
    font-size: 6.2em;
    opacity: 1;
  }
  70% {
    filter: blur(0.4rem);
    font-size: 6em;
    opacity: 1;
  }
  100% {
    opacity: 1;
    filter: blur(0rem);
    font-size: 6em;
  }
}

@keyframes slideanim{
  0% {
    opacity: 0;
    scale: (0.6);
    filter: blur(1.5rem);
  }
  20%{
    filter: blur(1.5rem);
    scale: (0.6);
    opacity: 1;
  }
  50%{
    filter: blur(0.8rem);
    scale: (0.8);
    opacity: 1;
  }
  70% {
    filter: blur(0.4rem);
    scale: (0.9);
    opacity: 1;
  }
  100% {
    opacity: 1;
    scale: (1);
    filter: blur(0rem);
  }
}

